<template>
	<div>
		<div class="title">热门推荐</div>
		<ul>
				<router-link tag="li" class="item border-bottom" 
				v-for="item of list" :key="item.id" :to="'/detail/'+item.id">
					<div class="item-img-wrapper">
						<img :src="item.imgUrl" class="item-img" alt="img" />
					</div>
					<div class="item-info">
						<p class="item-title">{{item.title}}</p>
						<p class="item-desc">
							{{item.desc}}
						</p>
						<button class="item-button">查看详情</button>
					</div>
				</router-link>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'HomeRecommend',
	props: {
		list: Array
	}
}
</script>

<style lang="stylus" scoped>
	@import '~styles/mixins.styl'
	.title
		margin-top: .2rem
		line-height: .8rem
		background: #eee
		text-indent: .2rem
	.item
		display: flex
		overflow: hidden
		height: 1.9rem
		.item-img
			width: 1.7rem
			height: 1.7rem
			padding: .1rem
		.item-info
			flex: 1
			padding: .1rem
			min-width: 0
			.item-title
				line-height: .54rem
				font-size: .32rem
				ellipsis()
			.item-desc
				line-height: .4rem
				color: #ccc
				ellipsis()
			.item-button
				margin-top: .1rem
				background: #ff9300
				padding: .1rem
				line-height: .4rem
				border-radius: .06rem
				color: #fff
</style>


